@import url(//fonts.googleapis.com/css?family=Lato:300:400);
@import url(//fonts.googleapis.com/css?family=Radley:300:400);
@import url(//fonts.googleapis.com/css?family=Rye:400);



body {
    margin: 0;
    background: rgba(84,58,183);
    font-family: 'Lato', sans-serif;
}

h1 {
    font-weight: 300;
    letter-spacing: 2px;
}

p {
    font-family: 'Lato', sans-serif;
    letter-spacing: 1px;
    font-size: 14px;
    color: #333333;
}

.header {
    position: relative;
    text-align: center;
    /* background: linear-gradient(60deg, rgba(84, 58, 183, 1) 0%, rgba(0, 172, 193, 1) 100%); */
    background: skyblue;
    color: white;
}

.logo {
    width: 50px;
    fill: white;
    padding-right: 15px;
    display: inline-block;
    vertical-align: middle;
}

.inner-header {
    height: 65vh;
    width: 100%;
    margin: 0;
    padding: 0;
}

.flex-container {
    /*Flexbox for containers*/
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.waves {
    position: relative;
    width: 100%;
    height: 15vh;
    margin-bottom: -7px;
    /*Fix for safari gap*/
    min-height: 100px;
    max-height: 100px;
}

.content {
    position: relative;
    height: 20vh;
    text-align: center;
    background-color: white;
}

/* Animation */

.parallax>use {
    animation: move-forever 25s cubic-bezier(.55, .5, .45, .5) infinite;
}

.parallax>use:nth-child(1) {
    animation-delay: -2s;
    animation-duration: 7s;
}

.parallax>use:nth-child(2) {
    animation-delay: -3s;
    animation-duration: 10s;
}

.parallax>use:nth-child(3) {
    animation-delay: -4s;
    animation-duration: 13s;
}

.parallax>use:nth-child(4) {
    animation-delay: -5s;
    animation-duration: 20s;
}

@keyframes move-forever {
    0% {
        transform: translate3d(-90px, 0, 0);
    }

    100% {
        transform: translate3d(85px, 0, 0);
    }
}

/*Shrinking for mobile*/
@media (max-width: 768px) {
    .waves {
        height: 40px;
        min-height: 40px;
    }

    .content {
        height: 30vh;
    }

    h1 {
        font-size: 24px;
    }
}

.boat-container-parent {
    width: 100vw;
    display: grid;
    grid-template-columns: repeat(1, 99vw);
    overflow: hidden;
    transform: translateY(9vh)
}

.boat-container {
    grid-column: 1;
    align-self: flex-end;
    transform: translateX(-40vh);
    animation: boat-sail 50s linear infinite;
}

.boat-top {
    width: 40vh;
    height: 39vh;
    display: flex;
    justify-content: space-around;
    transform: translateY(.2vh);
}

.sail1 {
    width: 0vh;
    height: 0vh;
    border-bottom: 36vh solid white;
    border-left: 24vh solid transparent;
}

.mast {
    background-color: rgb(200, 40, 40);
    width: 1.5vh;
    height: 38vh;
}

.sail2 {
    width: 0vh;
    height: 0vh;
    border-bottom: 36vh solid white;
    border-right: 12vh solid transparent;
}

.hull {
    width: 50vh;
    height: 0vh;
    border-top: 12vh solid rgb(200, 40, 40);
    border-left: 12vh solid transparent;
    border-right: 12vh solid transparent;
    transform: translateY(.2vh);
}

.port-holes {
    width: 20vh;
    display: flex;
    justify-content: space-between;
    transform: translate(1vh, -9vh);
}

.port-hole {
    background-color: gray;
    width: 4vh;
    height: 4vh;
    border-radius: 50%;
    border: 0.5vh solid white;
}

.boat-bottom {
    margin-left: 6vh;
    width: 28.2vh;
    height: 0vh;
    border-top: 4vh solid rgb(200, 60, 60);
    border-left: 3vh solid transparent;
    border-right: 3vh solid transparent;
    grid-column: 1;
    align-self: flex-start;
    transform: translateX(-40vh);
    animation: boat-sail 100s linear infinite;
}

@keyframes boat-sail {
    to {
        transform: translateX(100vw);
    }
}